.header{

    height: 300px;
    width: 100%;
    overflow-x: hidden ;
  
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 40px;
    align-items: center;
  
    form{
        width: 80%;
        text-align: center;
        
        
        
        input {
            height: 70px;
            width: 50%;
            border-radius: 65px;
            margin: 0 auto;
            padding: 30px;
            padding-left: 40px;
            
            border: 1px solid $color6;
            
            
            &::placeholder {
                font-size: 1.1rem;
                
            }
            
            &:focus {
                border: 1px solid $color8;
            }
        }
    }
        h3{
            font-size: 1.5rem;
            font-family: $font-2;
        }

        
    
}



.meal-card{
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    
}