.navigation{
    display: flex;
    justify-content: space-between;
    margin: 35px;
    
 
    h1{
        // font-family: ;
        margin-left: 15px;
        font-size: 1.8rem;
        color: $color5;
        
    }

    .nav-container {
        width: 25%;
       

        ul{
            display: flex;
            justify-content: center;
            gap: 50px;
            font-size: 0.95rem;

            a{
                list-style: none;
                color: $black;
                transition: 0.2s ease;
                letter-spacing: 0.8px;
             
               a:nth-child(2):hover::after{
                content: "";
                width: 25px;
                height: 2px;
                background: $color5;
                position: absolute;
               }

                &:hover::after {
                    content: "";
                    width: 45px;
                    height: 2px;
                    background: $color5;
                    position: absolute;
                    // transform: translateX(-20px);
                }

            }



          
        }
    }
  
}