@font-face {
    font-family: "font-1";
    src: "./assets/fonts/Poppins/Poppins-Light.ttf";

}

@font-face {
    font-family: "font-2";
    src: "./assets/fonts/Yuji_Mai/YujiMai-Regular.ttf";
}

$font-1: "font-1", sans-serif;
$font-2: "font-2", sans-serif;

$color1: #F5E8D7; /*Beige et crème*/
$color2: #FFFFFF; /*Blanc cassé 1*/
$color3: #f1f0ec; /*Blanc cassé 2*/
$color4: #A67543; /*Brun et caramel*/ 
$color5: #EC5025; /*Rouge orangé*/
$color6: #828181; /*Gris*/
$color7: #C0C0C0;  /*Argenté léger*/
$color8: #00BFFF;
$black: rgb(9, 11, 12);
$white: white;


*{
    margin: 0;
    padding: 0;
    font-family: $font-1;
    text-decoration: none;
    box-sizing: border-box;
    list-style: none;
    background: $color3 ;
}

body {
    min-height: 100vh;
}